.advantages {
  padding: 90px 0;
  text-align: center;

  @media (max-width: $mobile) {
    padding: 45px 0;
  }

  &__items {
    padding-top: 50px;
    padding-bottom: 60px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;

    @media (max-width: $mobile) {
      padding-top: 40px;
      padding-bottom: 0;
    }
  }

  &__item {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    width: calc(100% / 3 - 45px);
    margin-bottom: 35px;

    @media (max-width: $mobile) {
      width: 100%;
    }
  }

  &__icon {
    margin-bottom: 30px;
  }
}
