.menu {
  display: flex;
  flex-direction: column;
  align-self: flex-end;

  &__wrapper {
    display: flex;
    flex-wrap: wrap;
    margin-left: auto;
    margin-right: 0;

    @media (max-width: $mobile) {
      display: none;
    }
  }

  &__button {
    display: none;
    background-color: transparent;
    border: none;
    border-radius: 0;
    outline: none;
    cursor: pointer;

    &:focus,
    &:hover {
      outline: none;
    }

    @media (max-width: $mobile) {
      display: flex;
      justify-content: flex-end;
      align-items: flex-end;
      align-self: flex-end;
    }
  }

  &__link {
    padding: 13px 30px;
    margin-right: 42px;
    font-family: $secondaryFontExtraBold;
    color: $primary-color;
    text-decoration: none;

    &:last-child {
      margin-right: 0;
    }

    &:focus,
    &:hover {
      color: $default-color;
      background-color: $secondary-color;
      border-radius: 50px;
    }

    @media (max-width: $mobile) {
      font-size: 16px;
      line-height: 20px;
    }
  }
}
